/* ## Box Sizing
--------------------------------------------- */
html,
input[type="search"] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* ## Float Clearing
--------------------------------------------- */
.author-box,
.clearfix,
.entry,
.entry-content,
.footer-widgets,
.nav-primary,
.nav-secondary,
.pagination,
.site-container,
.site-footer,
.site-header,
.site-inner,
.widget,
.wrap {
  clear: both;
}

.author-box:before, .author-box:after,
.clearfix:before,
.clearfix:after,
.entry:before,
.entry:after,
.entry-content:before,
.entry-content:after,
.footer-widgets:before,
.footer-widgets:after,
.nav-primary:before,
.nav-primary:after,
.nav-secondary:before,
.nav-secondary:after,
.pagination:before,
.pagination:after,
.site-container:before,
.site-container:after,
.site-footer:before,
.site-footer:after,
.site-header:before,
.site-header:after,
.site-inner:before,
.site-inner:after,
.widget:before,
.widget:after,
.wrap:before,
.wrap:after {
  clear: both;
  display: block;
  content: '';
}

/* ## Typography
--------------------------------------------- */
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

::selection {
  background-color: #111111;
  color: #ffffff;
}

html {
  font-size: 62.5%;
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  background-color: #ffffff;
  color: #111111;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 15px;
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.82;
  letter-spacing: 0.01em;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-font-smoothing: grayscale;
  margin: 0;
  max-width: 100vw;
  overflow-x: hidden;
}

@media (min-width: 768px) {
  body {
    font-size: 1.6rem;
  }
}

body > div {
  font-size: 15px;
  font-size: 1.5rem;
}

@media (min-width: 768px) {
  body > div {
    font-size: 1.6rem;
  }
}

p {
  margin: 0 0 1.618em;
  padding: 0;
}

a {
  color: #aaaaaa;
  word-wrap: break-word;
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

a:hover {
  color: #111111;
}

ol,
ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

hr {
  border: 0;
  border-collapse: collapse;
  border-bottom: 1px solid #cccccc;
  border-bottom: 0.1rem solid #cccccc;
  clear: both;
  padding: 1em 0 0;
  margin: 0 0 2em;
}

b,
strong {
  font-weight: 400;
}

blockquote,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 1em 0;
  padding: 0 1em;
}

/* ## Headings
--------------------------------------------- */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Playfair Display", Helvetica, Arial, sans-serif;
  font-weight: 400;
  line-height: 1.236;
  letter-spacing: 0;
  margin: 0 0 0.618em;
  -webkit-font-smoothing: antialiased;
}

h1 {
  font-size: 2.6em;
  margin: 0 0 0.382em;
}

@media (min-width: 768px) {
  h1 {
    font-size: 3.6em;
  }
}

h2 {
  font-size: 2em;
}

@media (min-width: 768px) {
  h2 {
    font-size: 2.4em;
  }
}

h3 {
  font-size: 1.8em;
}

h4 {
  font-size: 1.4em;
}

h5 {
  font-size: 1.2em;
}

h6 {
  font-size: 1.1em;
}

/* ## Forms
--------------------------------------------- */
input,
select,
textarea {
  background-color: transparent;
  border: 1px solid #cccccc;
  border: 0.1rem solid #cccccc;
  border-radius: 0.1rem;
  padding: 0.5em 1em;
  width: 100%;
  line-height: 2;
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

input:focus,
select:focus,
textarea:focus {
  outline: none;
  border-color: #111111;
}

:focus {
  outline: 1px solid #cccccc;
  outline: 0.1rem solid #cccccc;
}

input[type="checkbox"],
input[type="image"],
input[type="radio"] {
  width: auto;
}

input[type="search"] {
  -webkit-appearance: none;
}

::-moz-placeholder {
  color: #111111;
  opacity: 1;
}

::-webkit-input-placeholder {
  color: #111111;
}

.button,
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  background-color: #111111;
  border: 0;
  border-radius: 9rem;
  color: #ffffff;
  cursor: pointer;
  font-size: 16px;
  font-size: 1.6rem;
  font-weight: 400;
  padding: 1.2em 2.4em;
  text-decoration: none;
  display: inline-block;
  width: auto;
  height: auto;
  line-height: 1;
  box-shadow: inset 0 0 20rem rgba(255, 255, 255, 0), 0 0 0 rgba(17, 17, 17, 0);
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

.button:after,
button:after,
input[type="button"]:after,
input[type="reset"]:after,
input[type="submit"]:after {
  content: "\f3d6";
  display: inline-block;
  font-family: "Ionicons";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  text-rendering: auto;
  line-height: 0;
  margin-top: 0.1em;
  margin-left: 0.618em;
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

.button:hover, .button:focus,
button:hover,
button:focus,
input[type="button"]:hover,
input[type="button"]:focus,
input[type="reset"]:hover,
input[type="reset"]:focus,
input[type="submit"]:hover,
input[type="submit"]:focus {
  color: #ffffff;
  box-shadow: inset 0 0 20rem rgba(255, 255, 255, 0.2), 0 2rem 1rem -1rem rgba(17, 17, 17, 0.1);
}

.button:hover:after, .button:focus:after,
button:hover:after,
button:focus:after,
input[type="button"]:hover:after,
input[type="button"]:focus:after,
input[type="reset"]:hover:after,
input[type="reset"]:focus:after,
input[type="submit"]:hover:after,
input[type="submit"]:focus:after {
  transform: translateX(5px);
}

.button.secondary,
button.secondary,
input[type="button"].secondary,
input[type="reset"].secondary,
input[type="submit"].secondary {
  background-color: #aaaaaa;
}

.button.secondary:hover,
button.secondary:hover,
input[type="button"].secondary:hover,
input[type="reset"].secondary:hover,
input[type="submit"].secondary:hover {
  background-color: #111111;
}

.button.small,
button.small,
input[type="button"].small,
input[type="reset"].small,
input[type="submit"].small {
  font-size: 12px;
  font-size: 1.2rem;
  padding: 1em 2em;
  white-space: nowrap;
}

.button:disabled, .button:disabled:hover,
button:disabled,
button:disabled:hover,
input[type="button"]:disabled,
input[type="button"]:disabled:hover,
input[type="reset"]:disabled,
input[type="reset"]:disabled:hover,
input[type="submit"]:disabled,
input[type="submit"]:disabled:hover {
  background-color: #f5f5f5;
  border-width: 0;
  color: #767676;
  cursor: not-allowed;
}

.button + button,
.button + .button,
button + button,
button + .button,
input[type="button"] + button,
input[type="button"] + .button,
input[type="reset"] + button,
input[type="reset"] + .button,
input[type="submit"] + button,
input[type="submit"] + .button {
  margin-top: 1em;
  clear: both;
}

@media (min-width: 512px) {
  .button + button,
  .button + .button,
  button + button,
  button + .button,
  input[type="button"] + button,
  input[type="button"] + .button,
  input[type="reset"] + button,
  input[type="reset"] + .button,
  input[type="submit"] + button,
  input[type="submit"] + .button {
    margin-top: 0;
    clear: none;
  }
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button {
  display: none;
}

fieldset {
  margin-bottom: 1em;
}

/* ## Objects
--------------------------------------------- */
embed,
iframe,
img,
object,
video,
.wp-caption,
.wp-video-shortcode {
  max-width: 100%;
  display: block;
}

img {
  height: auto;
}

figure {
  margin: 0;
}

video {
  object-fit: cover;
}

.wp-video-shortcode {
  width: auto !important;
}

/* ## Tables
--------------------------------------------- */
table {
  background-color: #ffffff;
  border: 1px solid #cccccc;
  border: 0.1rem solid #cccccc;
  border-collapse: collapse;
  border-spacing: 0;
  line-height: 1;
  margin-bottom: 40px;
  margin-bottom: 4rem;
  width: 100%;
}

tbody {
  border-bottom: 1px solid #cccccc;
  border-bottom: 0.1rem solid #cccccc;
}

td,
th {
  text-align: left;
  padding: 0.618em;
  border-top: 1px solid #cccccc;
  border-top: 0.1rem solid #cccccc;
  border-right: 1px solid #cccccc;
  border-right: 0.1rem solid #cccccc;
}

/* ## Accessibility
--------------------------------------------- */
.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut,
.wp-custom-logo .site-title,
.wp-custom-logo .site-description {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  height: 0.1rem;
  width: 1px;
  width: 0.1rem;
  border: 0;
  overflow: hidden;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus {
  clip: auto !important;
  height: auto;
  width: auto;
  display: block;
  padding: 10px 20px;
  padding: 1rem 2rem;
  z-index: 100000;
  text-decoration: none;
  box-shadow: 0 0 0.2rem 0.2rem rgba(17, 17, 17, 0.1);
}

.more-link {
  position: relative;
  font-style: normal;
}

.genesis-skip-link {
  margin: 0;
}

.genesis-skip-link li {
  height: 0;
  width: 0;
  list-style: none;
}

/* ## Site Containers
--------------------------------------------- */
.wrap {
  margin: 0 auto;
  padding-left: 5%;
  padding-right: 5%;
}

@media (min-width: 768px) {
  .wrap {
    width: 90%;
    max-width: 1280px;
    padding: 0;
  }
}

.wrap .wrap {
  max-width: 100%;
  width: auto;
  padding: 0;
}

.site-inner {
  clear: both;
  word-wrap: break-word;
}

.page-builder .site-inner {
  padding: 0;
  border-top: 1px solid #cccccc;
  border-top: 0.1rem solid #cccccc;
  border-bottom: 1px solid #cccccc;
  border-bottom: 0.1rem solid #cccccc;
}

.site-inner .wrap {
  max-width: 768px;
}

/* ## Content Widths and Positions
--------------------------------------------- */
@media (min-width: 896px) {
  .content {
    width: 100%;
  }
  .sidebar-primary {
    width: 25%;
  }
  .no-sidebar .content,
  .sidebar-content .content,
  .content-sidebar .content {
    width: calc(75% - 4rem);
  }
  .no-sidebar .content {
    margin: 0 auto;
  }
}

/* ## Column Classes (including fifths)
--------------------------------------------- */
