    font-size: 3rem;
  }
}

.menu-item {
  display: block;
  padding: 0.5em 0;
  width: 100%;
  position: relative;
  overflow: hidden;
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

.menu-item.right {
  margin-left: auto;
}

.menu-item a {
  text-decoration: none;
  display: inline-block;
  color: #ffffff;
  opacity: 0;
  margin-left: 1px;
  transform: translateY(140%);
  transition: opacity 0.55s cubic-bezier(0.75, 0, 0.175, 1), transform 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

.visible .menu-item a {
  opacity: 1;
  transform: translateY(0);
  transition-delay: 0.82s;
}

.menu-item a:hover,
.menu-item a:focus,
.menu-item.current-menu-item > a {
  color: #f5f5f5;
}

.menu-widget {
  padding: 2em 0;
  color: #ffffff;
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 13.819px;
  font-size: 1.382rem;
  line-height: 1.618em;
}

.menu-widget a {
  font-weight: 600;
  display: inline-block;
}

.menu-widget p {
  opacity: 0;
  transform: translateY(140%);
  transition: opacity 0.55s cubic-bezier(0.75, 0, 0.175, 1), transform 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

.menu-widget p:first-of-type {
  margin-top: 1em;
}

.visible .menu-widget p {
  opacity: 1;
  transform: translateY(0);
  transition-delay: 0.82s;
}

.menu-widget .alignleft {
  margin-left: -14px;
}

