  width: auto;
  margin: 0 1em;
}

.nav-secondary .menu-item a {
  transform: none;
  color: #111111;
  opacity: 1;
}

/* ## Menu Toggles
--------------------------------------------- */
.menu-toggle {
  padding: 27px 0;
  padding: 2.7rem 0;
  cursor: pointer;
  background-color: transparent;
  display: block;
  position: absolute;
  top: 20px;
  top: 2rem;
  padding: 0;
  right: 5%;
  line-height: 0;
  height: 30px;
  height: 3rem;
  width: auto;
  letter-spacing: -1em;
  color: transparent;
  z-index: 999;
  transition: none;
}

@media (min-width: 768px) {
  .menu-toggle {
    right: 0;
  }
}

.menu-toggle:hover, .menu-toggle:focus {
  background: none;
  outline: none;
  box-shadow: none;
  color: transparent;
}

.menu-toggle.activated {
  position: fixed;
  right: 5vw;
}

.menu-toggle.activated span,
.menu-toggle.activated span:before,
.menu-toggle.activated span:after {
  background-color: #f5f5f5;
}

@media (min-width: 1420px) {
  .menu-toggle.activated {
    right: calc(50vw - 640px);
  }
}

.menu-toggle span {
  display: block;
  position: relative;
  height: 2px;
  height: 0.2rem;
  background: #111111;
  transition: background 0s 0.4s;
  width: 25px;
  width: 2.5rem;
  margin: 0;
  border-radius: 0.1rem;
}

.menu-toggle span::before, .menu-toggle span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  height: 0.2rem;
  background-color: #111111;
  border-radius: 0.1rem;
  content: " ";
}

.menu-toggle span::before {
  top: -7px;
  top: -0.7rem;
  transition: top 0.4s 0.4s, transform 0.4s 0s, background-color 0.82s 0.1s;
}

.menu-toggle span::after {
  bottom: -7px;
  bottom: -0.7rem;
  transition: bottom 0.4s 0.4s, transform 0.4s 0s, background-color 0.82s 0.1s;
}

.menu-toggle.activated span {
  background: none;
}

.menu-toggle.activated span::before {
  top: 0;
  transform: rotate(135deg);
  transition: top 0.4s 0s, transform 0.4s 0.4s, background-color 0.82s 0.1s;
}

.menu-toggle.activated span::after {
  bottom: 0;
  transform: rotate(-135deg);
  transition: bottom 0.4s 0s, transform 0.4s 0.4s, background-color 0.82s 0.1s;
}

.sub-menu-toggle {
  position: absolute;
  right: 0;
  top: 1em;
  background: none;
  height: 30px;
  height: 3rem;
  width: 30px;
  width: 3rem;
  padding: 0;
  margin: 0;
  border-radius: 0;
}

.sub-menu-toggle:hover, .sub-menu-toggle:focus {
  background: none;
  outline: none;
}

.sub-menu-toggle:hover:after, .sub-menu-toggle:focus:after {
  margin: 0;
  transform: rotate(0);
}

.sub-menu-toggle:after {
  content: "\f489";
  margin: 0;
  transform: rotate(0);
}

.sub-menu-toggle.activated:after {
  content: "\f462";
  transform: rotate(0);
}

