/* ## Display Posts Shortcode
--------------------------------------------- */
.display-posts-listing {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto !important;
}

.display-posts-listing .listing-item {
  position: relative;
  list-style-type: none;
  margin-bottom: 6vw;
  box-shadow: 0 1rem 0rem -1rem rgba(17, 17, 17, 0);
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  clear: both;
}

.display-posts-listing .listing-item:before, .display-posts-listing .listing-item:after {
  clear: both;
  display: block;
  content: '';
}

@media (min-width: 768px) {
  .display-posts-listing .listing-item {
    margin-bottom: 2.5641%;
  }
}

.display-posts-listing .listing-item:before {
  position: absolute;
  background-color: rgba(17, 17, 17, 0);
  z-index: 1;
  pointer-events: none;
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.display-posts-listing .listing-item:hover, .display-posts-listing .listing-item:focus {
  box-shadow: 0 2rem 2rem -1rem rgba(17, 17, 17, 0.2);
}

.display-posts-listing .listing-item:hover:before, .display-posts-listing .listing-item:focus:before {
  background-color: rgba(17, 17, 17, 0.82);
}

.display-posts-listing .listing-item:hover .title, .display-posts-listing .listing-item:focus .title {
  opacity: 1;
  transform: translateY(20%);
  transition-delay: 0.25s;
}

.display-posts-listing .listing-item:hover .category-display a, .display-posts-listing .listing-item:focus .category-display a {
  opacity: 1;
  transform: translateY(-220%);
  transition-delay: 0.25s;
}

.display-posts-listing .image {
  display: block;
  position: relative;
  overflow: hidden;
}

.display-posts-listing img {
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

.display-posts-listing .fade-in {
  display: block;
  position: absolute;
  text-align: center;
  line-height: 1;
  height: 1.4em;
  z-index: 2;
  color: #ffffff;
  font-size: 26px;
  font-size: 2.6rem;
  overflow: hidden;
  font-family: "Playfair Display", Helvetica, Arial, sans-serif;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

.display-posts-listing .title {
  position: absolute;
  transform: translateY(110%);
  color: #ffffff;
  text-decoration: none;
  -webkit-font-smoothing: antialiased;
  opacity: 0;
  transition-delay: 0.25s;
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.display-posts-listing .category-display {
  position: absolute;
  text-align: center;
  text-transform: uppercase;
  font-size: 10px;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  pointer-events: none;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.display-posts-listing .category-display span {
  display: none;
}

.display-posts-listing .category-display a {
  text-decoration: none;
  margin: 0 0.5em;
  color: #f5f5f5;
  opacity: 0;
  transition-delay: 0.25s;
  transform: translateY(-100%);
  -webkit-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -moz-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  -o-transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
  transition: all 0.55s cubic-bezier(0.75, 0, 0.175, 1);
}

